import React, { Component }  from "react";
import { Link } from "react-router-dom";

class Header extends Component {

  constructor(props) {
    super(props);
    this.state = {
      menu: false
    };
    this.toggleMenu = this.toggleMenu.bind(this);
  }

  toggleMenu(){
    this.setState({ menu: !this.state.menu })
  }

  render (){

    const show = (this.state.menu) ? "show" : "" ;

    return(
    <nav className="navbar navbar-expand-lg fixed-top trans-navigation" style={{ paddingTop: "0" }}>
      <div className="container" style={{ paddingRight: "10px" }}>
        <Link className="navbar-brand" to="/">
          <img src="images/gallery_pics/eeesoc_nav_bar.png.png" alt="eeesoc_logo" className="img-fluid b-logo" />
        </Link>
        <button
          className="navbar-toggler"
          type="button"
          aria-controls="mainNav"
          aria-expanded="false"
          aria-label="Toggle navigation"
          onClick={ this.toggleMenu }
          style={{outline: 'none',
    boxShadow: 'none'}}
        >
          <span className="navbar-toggler-icon">
            <i className="fa fa-bars" />
          </span>
        </button>
        <div className={"collapse navbar-collapse justify-content-center " + show} id="mainNav" style={{ alignItems: "center" }}>
          <ul className="navbar-nav ">
            <li className="nav-item">
              <Link className="nav-link smoth-scroll" to="/" onClick={ this.toggleMenu }>
                Home
              </Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link smoth-scroll" to="/workshop" onClick={ this.toggleMenu }>
                Workshops
              </Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link smoth-scroll" to="/pp" onClick={ this.toggleMenu }> 
                Project Programmes
              </Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link smoth-scroll" to="/alumni" onClick={ this.toggleMenu }> 
                Alumni
              </Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link smoth-scroll" to="/gallery" onClick={ this.toggleMenu }>
                Gallery
              </Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link smoth-scroll" to="/contact" onClick={ this.toggleMenu }>
                Contact
              </Link>
            </li>
          </ul>
        </div>
      </div>
    </nav>);
}
}

export default Header;
