import React from "react";

function Projects({ data }) {
  const { title, intro, year, projects, dataTarget } = data;

  const getMargin = (index) => {
    return {
      marginTop: index === 2 ? "10px" : "auto",
      marginBottom: index === 2 ? "30px" : "10px",
    };
  };

  return (
    <div className="col-lg-12 col-sm-12 col-md-12">
      <div className="service-box p-5 rounded mb-12">
        <h4>{title}</h4>
        <div className="line"></div>
        <p>{intro}</p>
        <br />
        <h4 className="mt-5 mb-3">{`${title} - ${year}`}</h4>

        {/* Project Item */}
        <div className="row">
          {projects.map((project, index) => (
            <div className="col-lg-4 col-sm-6 col-md-6" key={index}>
              <div className="service-box">
                <div style={getMargin(index)}>
                  <img src={project.img} alt="service-icon" className="img-fluid" />
                </div>
                {/* Button trigger modal change value of data-target  */}
                <button
                  type="button"
                  style={{whiteSpace: "normal", wordWrap: "break-word"}}
                  className="btn btn-outline-info btn-sm"
                  data-toggle="modal"
                  data-target={`#${dataTarget}-${index}`}
                >
                  {project.name}
                </button>
                {/* Modal change exampleModalCenter(id) and exampleModalLongTitle0(id)everytime this is changed */}

                {/* Modal Content */}
                <div
                  className="modal fade"
                  id={`${dataTarget}-${index}`}
                  tabIndex="-1"
                  role="dialog"
                  aria-labelledby={`${dataTarget}-${index}-Title`}
                  aria-hidden="true"
                >
                  <div className="modal-dialog modal-lg modal-dialog-centered" role="document">
                    <div className="modal-content">
                      <div className="modal-header">
                        <h5 className="modal-title" id="exampleModalLongTitle6">
                          {project.title}
                        </h5>
                        <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                          <span aria-hidden="true">&times;</span>
                        </button>
                      </div>
                      <div className="modal-body">
                        <p>{project.about}</p>
                      </div>
                      <h5>Useful Links:</h5>
                      <a
                        style={{ color: "#8a2be2" }}
                        href={project.reportLink !== null ? project.reportLink : "#"}
                        target="_blank"
                        rel="noopener noreferrer"
                      >
                        Report
                      </a>
                      <div className="modal-footer">
                        <button type="button" className="btn btn-outline-danger btn-sm" data-dismiss="modal">
                          Close
                        </button>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
}

export default Projects;
