import React from "react";

function Mentor({ index, mentor }) {
  const { name, imgUrl, profile, designation, department, areas_of_interest } = mentor;
  return (
    <>
      {index === 0 && <div className="col-lg-1 col-sm-12 col-md-12"></div>}
      {index === 1 && <div className="col-lg-3 col-sm-12 col-md-12"></div>}
      <div className="col-lg-4 col-sm-12 col-md-12">
        <div className="service-box">
          <div>
            <img src={imgUrl} alt="service-icon" className="img-fluid" />
          </div>
          <div className="service-inner">
            <a href={profile} target="_blank" rel="noopener noreferrer">
              <h4>{name}</h4>
            </a>
            <p>
              {designation}
              <br />
              {department}
            </p>
            <p>Areas of Interest: {areas_of_interest}</p>
          </div>
        </div>
      </div>
      {index === 1 && <div className="col-lg-1 col-sm-12 col-md-12"></div>}
    </>
  );
}

export default Mentor;
