import React from "react";

function ExecutiveBodyMember({ index, member }) {
  const { name, img, linkedinUrl, designation } = member;
  return (
    <>
      {index % 2 === 0 && <div className="col-lg-1 col-sm-12 col-md-12"></div>}
      {index % 2 === 0 && index > 1 && <div className="col-lg-1 col-sm-12 col-md-12"></div>}

      {index % 2 !== 0 && <div className="col-lg-2 col-sm-12 col-md-12"></div>}

      <div className={index % 2 === 0 ? "col-lg-4 col-sm-12 col-md-12" : "col-lg-4 col-sm-6 col-md-4"}>
        {/* Member's info */}
        <div className="blog-block">
          <img src={img} alt={name} className="img-fluid img-circle" />
          <div className="blog-text text-center">
            <h4>
              <a href={linkedinUrl !== null ? linkedinUrl : "#"} target="_blank" rel="noopener noreferrer">
                {name}
              </a>
            </h4>
            <p>{designation}</p>
          </div>
        </div>
      </div>
    </>
  );
}

export default ExecutiveBodyMember;
